import React, { useRef, useLayoutEffect, useState } from "react";
import ReactPlayer from 'react-player';
import ProjectsNavBar from './projectsNavBar/ProjectsNavBar';
import './Techo.css';

const Techo = () => {


  const targetRef = useRef();
    const [dimensions, setDimensions] = useState({ width:0, height: 0 });



    useLayoutEffect(() => {
        if (targetRef.current) {
          setDimensions({
            width: targetRef.current.offsetWidth,
            height: targetRef.current.offsetHeight
          });
        }
      }, []);

  return (
    <div>
      <ProjectsNavBar/>
        <div className='project-container'>
        <h1>Techo Hiring</h1>
       <h2>Breve resumen</h2>
       <h4>Proyecto realizado grupalmente en Henry con el cul me gradu. El desafo fue continuar un Proyecto
        con cdigo heredado hecho 100% con TypeScript tanto en el back como en el front para la ONG TECHO.
        Yo particularmente realic rutas del backend, asociaciones, generar servicio de correos, componentes de React,
        enviar informacin del backend hacia el front y consumirlas correctamente.
       Por ltimo, me toc arreglar bugs generales tanto de back como de front.
       Por el momento, no se encuentra en produccin.
       </h4>
       <br/>
       <h2>Tecnologas utilizadas</h2>
       <h3>Backend</h3>
       <p>Se ejecuta en expressJs y se conecta a travs de la ORM Sequelize a PosgreSQL. Fueron utilizados
        mdulos nativos de nodeJs como fs y path para la correcta lectura y manejo de carpetas y archivos.
        Se utilizo jwt(json-web-token) para generar token de seguridad de acceso a los usuarios.
        Tambin se utiliz nodemailer y se puso en prctica el servicio de sendGrid para enviarles correo
         a los nuevos usuarios.
       </p>
       <h3>FrontEnd</h3>
       <p>Montado en ReactJs. se utilizan todos los hooks
        nativos de React para el correcto funcionamiento y la agilizacin de tareas. Se utiliza Redux como estado global
        del 80% de las peticiones hacia la api de la web. se utiliza mdulos como redux-persistor para no perder cierta
        info q necesitamos como el token.
        se utiliz bootstrap, reactstrap, Material-ui y Formik as como css puro para los estilos.
       </p>
       <br/>
       <a href='http://notFound' target='_blank'>Link an no disponible</a>
       <div className='video-container'>
        <ReactPlayer width={`${dimensions.width < 900 ? "500px" : "850px"}`} height={`${dimensions.width < 900 ? "296px" : "560px"}` } url='https://youtu.be/GMjGTJrUBJo'/>
       </div>
        </div>
    </div>
  )
}

export default Techo;